module.exports = "/* The side navigation menu */\n.sidenav {\n  height: 100%;\n  /* 100% Full-height */\n  width: 0;\n  /* 0 width - change this with JavaScript */\n  position: fixed;\n  /* Stay in place */\n  z-index: 999;\n  /* Stay on top */\n  top: 0;\n  /* Stay at the top */\n  left: 0;\n  background-color: #038896;\n  /* Black*/\n  overflow-x: hidden;\n  /* Disable horizontal scroll */\n  padding-top: 60px;\n  /* Place content 60px from the top */\n  transition: 0.2s;\n  /* 0.5 second transition effect to slide in the sidenav */ }\n.sidenav a {\n    padding: 8px 8px 8px 32px;\n    text-decoration: none;\n    font-size: 25px;\n    color: #ffffff;\n    display: block;\n    transition: 0.3s; }\n.sidenav a:hover {\n      color: #f1f1f1; }\n.sidenav .closebtn {\n    position: absolute;\n    top: 0;\n    left: -26px;\n    font-size: 36px;\n    margin-left: 50px; }\n.sidenav .last_menu li {\n    color: white;\n    font-size: 23px;\n    margin-bottom: 15px;\n    margin-top: 14px;\n    cursor: pointer;\n    transition: all 0.3s; }\n.sidenav .last_menu li:hover {\n      color: #8DC043; }\n.sidenav .last_menu li .sub li {\n      font-size: 15px;\n      margin-bottom: -9px;\n      color: #EFEFEF;\n      cursor: pointer; }\n.sidenav .social {\n    display: inline-flex;\n    position: relative;\n    top: 0px;\n    left: 1px; }\n.sidenav .social li {\n      margin-bottom: 12px; }\n.sidenav .social li fa-icon {\n        color: #ffffff !important; }\n/* Style page content - use this if you want to push the page content to the right when you open the side navigation */\n#main {\n  transition: margin-left .5s;\n  padding: 20px; }\n/* On smaller screens, where height is less than 450px, change the style of the sidenav (less padding and a smaller font size) */\n@media screen and (max-height: 450px) {\n  .sidenav {\n    padding-top: 15px; }\n  .sidenav a {\n    font-size: 18px; } }\n.First {\n  background-color: #038896;\n  padding: 16px 0px; }\n.First .tob-bar {\n    display: inline-flex; }\n.First .navbar-brand {\n    margin-left: 5%; }\n.First .setting li {\n    margin-left: 6%; }\n.First .setting li a {\n      position: relative; }\n.First .setting li a span {\n        position: absolute;\n        width: 9px;\n        height: 9px;\n        background-color: #8DC043;\n        border-radius: 100%;\n        right: 5px;\n        top: 12px; }\n@media (min-width: 768px) {\n  .sidenav .social {\n    display: block;\n    position: relative;\n    top: 256px; } }\n\n/*# sourceMappingURL=data:application/json;base64, */"